import React from 'react'
import { ConConfig } from '../Models/Config'
import { Estado, EstadoNull } from '../Models/General'
import Usuario, { conUsuario, userNull, UsuarioFila } from '../Models/Usuario'
import { formatoHora } from '../Utiles/Formatos'

interface State {
    cargando: boolean,
    fecha: string
    User: Usuario
    Permisos: Array<any>
    seccion: boolean
    usuarios: UsuarioFila[]
    estUsuarios: Estado
    estados: {
        Cliente: number,
        Compra: number,
        Config: number,
        Cotizacion: number,
        Departamento: number,
        Dolares: number,
        Factura: number,
        Nota: number,
        Orden: number,
        Plantilla: number,
        Producto: number,
        Proveedor: number,
        Tipo: number,
        Unidad: number,
        Usuario: number,
        Moneda: number,
        Ajuste: number,
    }
}
export interface SeccionType {
    data: State,
    logout: () => void,
    usuarios: UsuarioFila[]
    user: () => void,
    atcUsuario: () => void
}
export const SeccionContext = React.createContext<SeccionType | null>(null)

export class SeccionProvider extends React.Component<any, State> {
    interval: any;
    force_updater: any;
    usuarios = 0;
    constructor(props: any) {
        super(props)
        this.state = {
            cargando: true,
            User: userNull,
            fecha: '',
            Permisos: [],
            seccion: true,
            usuarios: [],
            estUsuarios: EstadoNull,
            estados: {
                Cliente: 10,
                Compra: 10,
                Config: 10,
                Cotizacion: 10,
                Departamento: 10,
                Dolares: 10,
                Factura: 10,
                Nota: 10,
                Orden: 10,
                Plantilla: 10,
                Producto: 10,
                Proveedor: 10,
                Tipo: 10,
                Unidad: 10,
                Usuario: 10,
                Moneda: 10,
                Ajuste: 10,
            }
        }
    }
    render() {
        this.atcUsuario();
        return <SeccionContext.Provider value={{
            data: this.state,
            logout: this.logout.bind(this),
            usuarios: this.state.usuarios,
            user: this.user.bind(this),
            atcUsuario: this.atcUsuario.bind(this),
        }}>
            {this.props.children}
        </SeccionContext.Provider>
    }
    async logout() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/close', { headers: { 'Authorization': 'Bearer ' + token } });
            localStorage.removeItem('Authorization');
            await response.json();
            //cargar nuevos datos
            this.user();
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    async user() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/me', { headers: { 'Authorization': 'Bearer ' + token } });
            let data: Usuario = await response.json();
            //cargar nuevos datos
            this.setState({ User: data, Permisos: data.permisos, seccion: data.id_usuario !== 0, cargando: false })
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    atcUsuario() {
        if (this.usuarios !== this.state.estados.Usuario) {
            this.usuarios = this.state.estados.Usuario;
            if (this.state.estUsuarios.status !== 2) {
                this.setState({ estUsuarios: { status: 2, hora: '...ACTUALIZANDO' } })
                conUsuario().cargarListaWeb(usuarios => this.setState({
                    usuarios,
                    estUsuarios: { status: 3, hora: formatoHora(undefined) }
                }));
            }
        }
    }
    componentDidUpdate() {
        const { state: { seccion, cargando }, interval } = this;
        if ((interval === undefined) && seccion && !cargando)
            this.initUpdater();
    }
    componentDidMount() {
        this.user();
    }
    initUpdater() {
        this.atcUsuario();
        conUsuario().cargarListaCache(usuarios => this.setState({ usuarios }));
        this.interval = setInterval(() => ConConfig('estado').pull(data => {
            if (JSON.stringify(this.state.estados) !== JSON.stringify(data))
                this.setState({ estados: data })
        }), 5 * 1000)
        let nun = 10;
        this.force_updater = setInterval(() => {
            nun += 1;
            let estados = { Cliente: nun, Compra: nun, Config: nun, Cotizacion: nun, Departamento: nun, Dolares: nun, Factura: nun, Nota: nun, Orden: nun, Plantilla: nun, Producto: nun, Proveedor: nun, Tipo: nun, Unidad: nun, Usuario: nun, Moneda: nun, Ajuste: nun, }
            this.setState({ estados })
        }, 10 * 360 * 1000)
    }
    componentWillUnmount() { clearInterval(this.interval); clearInterval(this.force_updater) }

}
export const SeccionConsumer = SeccionContext.Consumer;
